@import url(https://fonts.googleapis.com/css?family=Ubuntu:400,700);@import url(https://fonts.googleapis.com/css?family=Abel);
html {
  margin: 0px;
  height: 100%;
  width: 100%;
}
html body {
    margin: 0px;
    min-height: 100%;
    width: 100%;
    background-color: #283149;
    font-family: "Abel", serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 1.4rem;
    color: #dbedf3;
}
html body h1, html body h2, html body h3, html body h4, html body h5, html body h6 {
      font-family: "Ubuntu", sans-serif;
}
html body nav {
      font-size: 0.8rem;
}
html body a {
      color: #f7b633;
      text-decoration: none;
      position: relative;
      -webkit-transition: color 0.5s cubic-bezier(0, 0, 0, 1);
      transition: color 0.5s cubic-bezier(0, 0, 0, 1);
}
html body a::after {
        position: absolute;
        content: '';
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: #dbedf3;
        z-index: -1;
}
html body a:hover {
        color: #dbedf3;
}
html body a:hover::after {
          background-color: rgba(247, 182, 51, 0.2);
          height: 53%;
          -webkit-transition: height 0.2s cubic-bezier(0, 0, 0, 1);
          transition: height 0.2s cubic-bezier(0, 0, 0, 1);
}
html body a:focus, html body a:active {
        color: #f7b633;
}
html body a:focus::after, html body a:active::after {
          height: 0;
          -webkit-transition: height 0s, background-color 0.2s cubic-bezier(0, 0, 0, 1);
          transition: height 0s, background-color 0.2s cubic-bezier(0, 0, 0, 1);
}

h1[data-v-2c4ab7ca], h2[data-v-2c4ab7ca] {
  font-weight: normal;
}
ul[data-v-2c4ab7ca] {
  list-style-type: none;
  padding: 0;
}
li[data-v-2c4ab7ca] {
  display: inline-block;
  margin: 0 10px;
}

















